export default {
  name: "Major",
  data() {
    return {
      msg: this.optionMsg
    };
  },
  methods: {
    // 全部选项选择完毕后，会触发 finish 事件
    onFinish({
      selectedOptions
    }) {
      this.msg.show = false;
      this.msg.fieldValue = selectedOptions.map(option => option.text).join("/");
      if (this.msg.name == 'major') {
        this.$store.state.searchMsg[this.msg.name] = this.msg.fieldValue.split('/');
      } else {
        this.$store.state.searchMsg[this.msg.name] = this.msg.fieldValue;
      }
    }
  },
  props: ["optionMsg"],
  mounted() {}
};