import "core-js/modules/es.array.push.js";
import axios from 'axios';
export default {
  data() {
    return {
      data: this.data1,
      disabled: false
    };
  },
  methods: {
    add() {
      if (!localStorage.getItem("authorization")) {
        this.$router.push({
          name: "indexLogin"
        });
      } else {
        this.disabled = true;
        axios.get("/api/add", {
          params: {
            id: this.data.id
          },
          headers: {
            authorization: localStorage.getItem("authorization")
          }
        }).then(res => {
          if (!res.data.success) {
            alert("你已经添加成功");
          }
        }).catch(err => {
          console.log(err);
        });
      }
    },
    getDetail(data) {
      this.$router.push({
        name: 'detail',
        params: {
          data
        }
      });
    }
  },
  props: ["data1"]
};