import Vue from "vue"
import VueRouter from "vue-router"
Vue.use(VueRouter)
import Person from "../Pages/Person/Person"
import LoginIndex from "../Pages/Login/index"
import Home from "../Pages/Home/Home"

import Login from "../Pages/Login/Login"
import Rigist from "../Pages/Login/Rigist"
import Search from "../Pages/Search/Search"
import SearchDetail from "../Pages/SearchDetail/SearchDetail"

import Detail from "../Pages/SearchDetail/Detail"
const router=new VueRouter({
  routes: [
    
    {
      name: "indexLogin",
      path: "/index",
      component: LoginIndex,
      children:[
         {
          name:'login',
          path:"login",
          component:Login
         },
         {
          name:'rigist',
          path:"rigist",
          component:Rigist
         }
      ]
    },
    {
      name:"Home",
      path:"/home",
      component:Home,
      children:[
        {   
            name:"person",
            path: "person",
            component: Person,
            meta: { requiresAuth: true }
        },
        {
          name:"search",
          path:"search",
          component:Search
        }
      ]
    },
    {
      name:"SearchDetail",
      path:'/SearchDetail',
      component:SearchDetail
    },{
       name:"detail",
       path:"/detail",
       component:Detail
    }
  ]
})

router.beforeEach((to, from, next) => {
  const isAuthenticated = localStorage.getItem("authorization");
  if (to.meta.requiresAuth){
    // 需要登录的路由
    if (!isAuthenticated&&from.name!='Login') {
      // 未登录，跳转到登录页面，并将当前路由作为参数传递
      next({
        name: "indexLogin",
        query: { redirect: to.fullPath }
      });
    } else {
      // 已登录，直接进入该路由
      next();
    }
  } else {
    // 不需要登录的路由，直接进入
    next();
  }
});

export default router
