import axios from "axios";
export default {
  data() {
    return {
      username: '',
      password: '',
      message: ""
    };
  },
  methods: {
    onSubmit(values) {
      const usermsg = {
        username: this.username,
        password: this.password
      };
      axios.post('/api/login', usermsg).then(res => {
        if (!res.data.success) {
          this.message = res.data.msg;
        } else {
          let data = res.data;
          localStorage.setItem("authorization", data.authorization);
          this.$store.state.useradd = data.data.data;
          this.$router.replace({
            name: "person"
          });
        }
      }, err => {
        console.log(err);
      });
    }
  }
};