import Vuex from "vuex"

import Vue from "vue"
import axios from "axios"
Vue.use(Vuex)
const actions={
  getPositions(context,value){
    context.state.searchRes={}
    const obj=context.state.searchMsg
    // const {major, education, political_status, minimum_working_years, grassroots_experience } = context.searchMsg
    const major=obj.major
    const education=obj.education
    const political_status=obj.political_status
    const minimum_working_years=obj.minimum_working_years
    const grassroots_experience=obj.grassroots_experience
    const searchMsg={major, education, political_status, minimum_working_years, grassroots_experience}
    axios.get('/api/getPostions',{params:searchMsg}).then(
      (res)=>{
         context.commit("SETMSG",res.data.data)
      }
    ).catch((err)=>{
      context.state.err=true
    })
  }
}

const mutations={
  SETMSG(state,val){
    state.searchRes=val
    state.loading=false
  }
}

const state={
  searchMsg:{
  },
  searchRes:{
     
  },
  useradd:[],
  loading:true,
  err:false
}
const getters={
  mySearchRes(state){
    return state.searchRes
  }
}
export default new Vuex.Store({
    actions,
    mutations,
    state,
    getters
})

