import axios from 'axios';
import MiniSearch from './MiniSearch.vue';
export default {
  data() {
    return {
      value: '',
      isFirst: true
    };
  },
  components: {
    MiniSearch
  },
  mounted() {
    if (this.isFirst) {
      if (localStorage.getItem("authorization")) {
        axios.get("/api/getAdd", {
          headers: {
            authorization: localStorage.getItem("authorization")
          }
        }).then(res => {
          this.$store.state.useradd = res.data.data;
          this.isFirst = false;
        });
      }
    }
  }
};