import axios from 'axios';
export default {
  name: "rigist",
  data() {
    return {
      message: "",
      username: "",
      phone: "",
      password: '',
      password2: "",
      timer: null
    };
  },
  methods: {
    isRight() {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        if (this.password !== this.password2) {
          this.message = '密码不一致';
        } else {
          this.message = '';
        }
      }, 500);
    },
    submit() {
      const usermsg = {
        username: this.username,
        password: this.password,
        phone: this.phone
      };
      axios.post('/api/rigist', usermsg).then(res => {
        if (!res.data.success) {
          this.message = res.data.msg;
        } else {
          this.$router.replace({
            name: "login"
          });
        }
      }, err => {});
    }
  }
};